
import React from 'react';

class App extends React.Component
{
    constructor(){
        super();
        this.state= {count:0};
    }
    max1 = null;
    inpu = (e) =>
    {
        this.max1=e.target.value;
    }
    incr=()=>{
        
        
        if(this.max1!==null)
        {
        if(this.state.count>=this.max1)
            {
            alert("Max-Limit-Reached");

            } else this.setState({count:this.state.count+1}); 
        }else {alert("Please specify Max limit");}
            };
            
    
    decr=()=>{
        if(this.state.count<=0)
        {alert("Zero Reached Cannot decrement");}
        else
        this.setState({count:this.state.count-1})

    };
    render()
    {
        return(
            <div>
                <h1>Count is {this.state.count}</h1>
                <button onClick={this.incr}>Increment</button>
                <button onClick={this.decr}>Decrement</button>
                <h2>Max limit is</h2>
                <input type="text" onChange={this.inpu} name="max-limit" ref="max1" placeholder="Enter max-limit here"/>
                </div>
        );

    }
}

export default App;